<template>
  <v-form ref="form" v-model="valid" lazy-validation>
    <v-alert
      :value="successAlert"
      type="success"
    >
      {{ responseMessage }}
    </v-alert>
    <v-text-field
      v-model="contactForm.name"
      :rules="nameRules"
      :counter="20"
      label="Name"
      required
    ></v-text-field>
    <v-text-field
      v-model="phone"
      :rules="phoneRules"
      :mask="mask"
      label="Phone"
      required
    ></v-text-field>
    <v-textarea
      box
      label="Message"
      auto-grow
      :counter="250"
      :rules="msgRules"
      v-model="msg"
    ></v-textarea>

    <v-btn
      :disabled="!valid"
      @click="submit"
    >
      submit
    </v-btn>
    <v-btn @click="clear">clear</v-btn>
  </v-form>
</template>

<script>
import axios from 'axios'

export default {
  name: 'ContactForm',
  computed: {
    contactForm () {
      return this.$store.getters.contactForm
    }
  },
  data: () => ({
    successAlert: false,
    responseMessage: '',
    mask: '+# (###) ### - ####',
    value: '44444444444',
    valid: true,
    name: '',
    nameRules: [
      v => !!v || 'Name is required',
      v => (v && v.length >= 3) || 'Name must be more than 3 characters'
    ],
    phone: '',
    phoneRules: [
      v => !!v || 'Phone is required'
    ],
    msg: '',
    msgRules: [
      v => (v && v.length <= 250) || 'Message must be less than 250 characters'
    ]
  }),
  methods: {
    submit () {
      if (this.$refs.form.validate()) {
        axios.get('https://tgnwest.github.io/response.json')
          .then(response => {
            this.$store.dispatch('updateContactForm', { name: this.contactForm.name, phone: this.phone, message: this.msg })
            this.responseMessage = response.data.message
            this.successAlert = true
          })
          .catch(e => {
            console.log('error')
          })
      }
    },
    clear () {
      this.$refs.form.reset()
    }
  }
}

</script>
