<template>
  <v-app :dark="switch1">

    <the-header></the-header>

    <v-content>
      <v-container fluid>
        <router-view></router-view>
      </v-container>
    </v-content>

    <div id="free-space" style="height: 300vh"></div>

    <v-footer app>
      <v-switch :label="changeTheme" v-model="switch1"></v-switch>
    </v-footer>

  </v-app>
</template>

<script>
import TheHeader from '@/components/TheHeader'

export default {
  name: 'App',
  components: {
    TheHeader
  },
  computed: {
    changeTheme: function () {
      return this.switch1 ? 'Disable dark mode' : 'Enable dark mode'
    }
  },
  data () {
    return {
      switch1: false
    }
  }
}
</script>
