import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    contactForm: {
      name: 'Alex',
      phone: '',
      message: ''
    }
  },
  actions: {
    updateContactForm ({ commit }, form) {
      commit('UPDATE_CONTACT_FORM', form)
    }
  },
  mutations: {
    UPDATE_CONTACT_FORM (state, form) {
      state.contactForm = form
    }
  },

  getters: {
    contactForm (state) {
      return state.contactForm
    }
  }
})
